.student-card {
    width: 20vw; 
    height: auto;
    padding: 10px;
    margin: 10px;
    text-align: center;
    position: relative; /* To position the LinkedIn icon */
    background-color: #FFF8E1; /* Match the background color */
    transition: transform 0.3s ease-in-out;
}

.student-card img {
    width: 100%;
    border-radius: 16px;
    object-fit: cover;
    margin-bottom: 5px;
    transition: transform 0.3s ease-in-out;
}

.student-card h3 {
    margin: 10px 0 5px 0;
    font-size: 1.2rem;
    font-weight: bold;
}

.student-card p {
    margin: 0;
    font-size: 1rem;
    color: #555;
}

.linkedin-icon {
    position: absolute;
    /* top: 10px; */
    right: 8px; 
    margin: 8px 8px 0 0;
    background-color: #FFF8E1;
    padding: 4px 8px 4px 8px;
    border-radius: 12px; 
    transition: transform 0.3s ease-in-out;
    font-size: 1.5rem; 
}

.linkedin-icon:hover {
    background-color: #e0d6b6; 
}

.linkedin-icon a {
    color: #007bff;
    text-decoration: none;
}

.linkedin-icon a:hover {
    color: #483e1e; 
}

.student-card:hover {
    transform: translateY(-5px); 
}

/* Responsive Design */
@media (max-width: 768px) {
    .student-card {
        width: 80%;
    }

    .linkedin-icon {
        font-size: 1.2rem;
        padding: 8px;
    }
}

@media (max-width: 480px) {
    .student-card {
        width: 90%; 
    }

    .linkedin-icon {
        font-size: 1rem;
        padding: 6px;
    }
}
